import React from "react";
import CountUp from "react-countup";

interface IStatSection {
	topTitle: string;
	percentage: number;
	bottomTitle: string;
}

const Percentage = ({ percentage }: { percentage: number }) => (
	<div className="font-bold text-[3.5vw] stat-percentage mb-1">
		<CountUp start={0} end={percentage} duration={3} />%
	</div>
);

const StatSectionComp = ({
	topTitle,
	percentage,
	bottomTitle,
}: IStatSection) => (
	<div className="flex flex-col items-center justify-center text-center col-span-4">
		<p>{topTitle}</p>
		<div className="flex mt-5 flex-col justify-center items-center">
			<Percentage percentage={percentage} />
			<div>{bottomTitle}</div>
		</div>
	</div>
);

export const StatSection = () => (
	<div className="col-span-12 order-4">
		<div className="md:h-[200px] grid grid-cols-12 bg-orange-500 px-10 tems-center text-white gap-5">
			<StatSectionComp
				topTitle=""
				percentage={18}
				bottomTitle="are stunted, or their height too short for their age"
			/>
			<StatSectionComp
				topTitle=""
				percentage={6}
				bottomTitle="wasted, or too thin for their height"
			/>
			<StatSectionComp
				topTitle=""
				percentage={37}
				bottomTitle="are vitamin A deficient, leading to lower immunity and eyesight."
			/>
		</div>
	</div>
);
