import React, { useEffect, useState, useContext } from "react";
import { Transition } from "react-transition-group";
import { UnicefBanner } from "../UnicefBanner";
import cl from "classnames";
import { TIMEOUT } from "../../constants";
import { WindowEffectContext } from "../../contexts";

export const TopSection = () => {
	const [isVisible, setIsVisible] = useState<boolean>(false);
	const { isEffectFinished } = useContext(WindowEffectContext);

	useEffect(() => {
		if (isEffectFinished) {
			setIsVisible(true);
		}
	}, [isEffectFinished]);

	return (
		<div className="grid grid-cols-12">
			<div className="col-span-12 md:col-span-4 order-1">
				<Transition
					in={isVisible}
					timeout={TIMEOUT}
					mountOnEnter
					unmountOnExit
				>
					{(state: any) => (
						<img
							src="assets/donors-1.png"
							alt="boy"
							className={cl(
								"md:w-[80%] shadow-xl transition-all duration-500",
								state === "entered"
									? "rotate-[-15deg]"
									: "rotate-0"
							)}
						/>
					)}
				</Transition>
			</div>
			<div className="col-span-12 md:col-span-4 order-2">
				<div className="flex justify-center items-center flex-col text-center text-orange-500">
					<UnicefBanner
						type="small"
						isAbsolute={false}
						withTransition={true}
						transitionDirection="top"
					/>
					<div>
						<h1 className="uppercase text-[4.2vw] font-bold m-2 donors-heading">
							Nutrition now
						</h1>
						<h4 className="text-[1.4vw] font-bold mx-10">
							RE-IMAGINING NUTRITION FOR EVERY CHILD IN THE SAHEL
						</h4>
					</div>
				</div>
			</div>
			<div className="col-span-12 md:col-span-4 order-3 flex flex-col items-end">
				<Transition
					in={isVisible}
					timeout={TIMEOUT}
					mountOnEnter
					unmountOnExit
				>
					{(state: any) => (
						<img
							src="assets/donors-2.png"
							alt="donors-2"
							className={cl(
								"w-[80%] shadow-xl transition-all duration-500",
								state === "entered"
									? "rotate-[10deg]"
									: "rotate-0"
							)}
						/>
					)}
				</Transition>
			</div>
		</div>
	);
};
